let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let header = document.querySelector("header")
let navBg = document.querySelector(".nav__bg")

if(burger) {
    burger.onclick = function () {
        header.classList.toggle("_open")
        body.classList.toggle("fixed-body")
    }
}

navBg.onclick = function(){
    header.classList.remove("_open")
    body.classList.remove("fixed-body")
}

let contactBtn = document.querySelector(".contact__btn")
let name = document.querySelector("#name")
let email = document.querySelector("#email")
let contactForm = document.querySelector(".contact__form")

function validateEmail(email) {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}


contactBtn.onclick = function (e) {
    e.preventDefault()
    let nameIsValid = false
    let emailIsValid = false
    if(name.value === ""){
        name.classList.add("invalid-input")
        nameIsValid = false
    } else{
        name.classList.remove("invalid-input")
        nameIsValid = true
    }

    if(email.value === ""){
        email.classList.add("invalid-input")
        emailIsValid = false
    } else{
        email.classList.remove("invalid-input")
        emailIsValid = true
    }

    if(validateEmail(email.value)){
        emailIsValid = true
    } else{
        emailIsValid = false
    }


    if(nameIsValid && emailIsValid){
        contactForm.submit();
    }
}



